@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);

body {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: #000000;
  margin: 50px;
  color : white;  
}

.white-color{
  color:white;
}

.signin-background{
  background-color: #000000;
  border: 2px solid #fe4452;
  height: auto;
  width: 90%;
  max-width: 350px;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  /* box-shadow: 0px 40px 30px -20px rgba(0, 0, 0, 0.3); */
  text-align: center;  
}

.login-background{
  background-color: #000000;
  border: 2px solid #93ff67;
  height: auto;
  width: 90%;
  max-width: 350px;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  /* box-shadow: 0px 40px 30px -20px rgba(0, 0, 0, 0.3); */
  text-align: center;  
}

.background1 {
  background-color: #000000;
  height: auto;
  width: 90%;
  max-width: 350px;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  /* box-shadow: 0px 40px 30px -20px rgba(0, 0, 0, 0.3); */
  text-align: center;
}

.col-form {
  display: flex;
  padding: 30px;
  flex-direction: column;
}

.login { /* 로그인 아래 선 같은 거 */
  font-family: 'Noto Sans KR', sans-serif;
  border: none;
  border-bottom: 1px solid #ffffff;
  background: none;
  padding: 10px 0;
  font-weight: 700;
  transition: .25;
  width: 85%;
  color: white;
}

.login:active,
.login:focus,
.login:hover {
  outline: none;
  border-bottom-color: #93ff67;
}
.login-btn {
  font-family: 'Noto Sans KR', sans-serif;
  border: none;
  width: 85%;
  background-color: #fe4452;
  color: black;
  padding: 10px 0;
  font-weight: 700;
  border-radius: 5px;
  cursor: pointer;
  transition: .2s;
}
.login-btn:hover {
  background-color: white;
}

.signin-btn{
  font-family: 'Noto Sans KR', sans-serif;
  border: none;
  width: 85%;
  background-color: #93ff67;
  color: black;
  padding: 10px 0;
  font-weight: 700;
  border-radius: 5px;
  cursor: pointer;
  transition: .2s;
}

.signin-btn:hover {
  background-color: white;
}

.signupbtn {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  margin-left: 10px;
}

.loginbtn {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  margin-left: 10px;
}