.navbar{
    width:100%;
    background-color: black;
    padding: 15px 0px;
    text-align: center;
}

.navbarMenu{
    color: white;
    margin : 8px;
    text-decoration: none;
    margin-left : 100px;    
}

.row-form{
    flex-direction : row;
}

.search-background{
    border: 2px solid #fe4452;
    border-radius: 30px;    
    margin-left : 100px;
}

.search-input{
    color : white;
    background: none;
    border: none;
    width: 400px;
    margin: 10px 0;
    outline : none;

}

.tdSize {
    width: 200px;
    height: 200px;
  }

  .td400 {
    width: 400px;
    height: 50px;
    white-space : normal;
  }  

  .chart-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  
  .chart-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .chart-table td {
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  .even-row {
    background-color: #f9f9f9;
  }
  
  .odd-row {
    background-color: #ffffff;
  }
  
  .rank-cell {
    width: 50px;
  }
  
  .item-cell {
    width: 100px;
  }